<template>
  <div class="container1">
    <div class="qna-write-container">
      <form class="qna-write-form" autocomplete="off">
        <h2>Q&A </h2>
        <!--  -->
        <div class="info-input-container">
          <div class="qna-label-area">
            <label for="qna-category"></label>
          </div>
          <div class="input-area">
            <input
              class="input-text"
              type="text"
              id="qna-category"
              :value="myQnA.category === 'usage' ? '' : ''"
              readonly
            />
          </div>
        </div>

        <!--  -->
        <div class="info-input-container">
          <div class="qna-label-area">
            <label for="qna-title"></label>
          </div>
          <div class="input-area">
            <input
              class="input-text"
              type="text"
              name="qna-title"
              id="qna-title"
              :value="myQnA.title"
              readonly
            />
          </div>
        </div>

        <!--  -->
        <div class="info-input-container">
          <div class="qna-label-area">
            <label for="qna-content"></label>
          </div>
          <textarea
            class="input-text"
            id="qna-content"
            cols="88"
            rows="10"
            v-model="myQnA.contents"
            readonly
          ></textarea>
        </div>

        <!--   -->
        <div class="info-input-container">
          <div class="qna-label-area">
            <label for="qna-content" id="answered"></label>
          </div>
          <textarea
            class="input-text"
            id="qna-content"
            cols="88"
            rows="10"
            v-model="myQnA.answer"
            readonly
          ></textarea>
        </div>

        <div class="qna-btn-group">
          <button class="big-ctlbtn cancle-btn" type="button" @click="cancel">
            
          </button>
          <!--    '', ''  -->
          <button
            class="big-ctlbtn update-btn"
            type="button"
            @click="qnaUpdate"
          >
            
          </button>
          <button class="big-ctlbtn delete-btn" type="button"></button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
export default {
  name: "WriteQnA",
  data() {
    return {
      myQnA: {
        category: "usage",
        title: "    ?!?!",
        contents: " !",
        answer: "  !",
      },
    };
  },
  methods: {
    cancel() {
      this.$router.go(-1); // 
    },
    qnaUpdate() {
      this.$router.push("/UpdateQnA");
    },
  },
  created() {
    this.$emit("bgImage", "type3");
    this.selectCategory = this.myQnA.category;
  },
};
</script>
<style scoped>
@import url("@/css/client/qna/qnaForm.css");
.qna-btn-group > .update-btn {
  margin-right: 20px;
}
</style>
