<template>
  <div class="container1">
    <div class="qna-container">
      <div class="faq-container">
        <h2 class="faq-title">Q&A </h2>
      </div>
      <div class="nav-bar">
        <!-- exact-active-class : Vue Router   -->
        <!-- router-link  (exact match)        -->
        <router-link to="/QnA" class="nav-item" exact-active-class="active"
          >Q&A</router-link
        >
        <router-link to="/myQnA" class="nav-item" exact-active-class="active"
          >  </router-link
        >
        <!-- ...   ... -->
      </div>
      <table class="qna-table">
        <thead>
          <tr>
            <th class="qna-tr"></th>
            <th class="qna-tr"></th>
            <th></th>
            <th></th>
            <th class="qna-tr"></th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="item in paginatedQnA"
            :key="item.no"
            :class="{
              noticeTR: item.category === '',
            }"
          >
            <!-- category       -->
            <td class="qna-tr">{{ item.no }}</td>
            <td class="qna-tr">{{ item.category }}</td>
            <td>
              <a href="/DetailQnA">{{ item.title }}</a
              ><span
                class="qna-badge"
                :class="{
                  notAnswered: item.progress === '',
                  answered: item.progress === '',
                }"
                >{{ item.progress }}</span
              >
            </td>
            <td class="qna-tr">{{ item.writer }}</td>
            <td class="qna-tr">{{ item.date }}</td>
          </tr>
        </tbody>
      </table>

      <div class="pagination">
        <button
          class="pagination-button"
          :class="{ disabled: currentPage === 1 }"
          @click="setCurrentPage(currentPage - 1)"
          v-if="currentPage > 1"
        >
          &lt;
        </button>

        <button
          v-for="page in pageCount"
          :key="page"
          :class="{ 'pagination-button': true, active: currentPage === page }"
          @click="setCurrentPage(page)"
        >
          {{ page }}
        </button>

        <button
          class="pagination-button"
          :class="{ disabled: currentPage === pageCount }"
          @click="setCurrentPage(currentPage + 1)"
          v-if="currentPage < pageCount"
        >
          &gt;
        </button>
      </div>
      <div class="qna-search-container">
        <form id="qna-search-form" @submit.prevent="onSearch">
          <div class="qna-search-area">
            <input
              class="qna-search-input"
              v-model="searchQuery"
              type="text"
              placeholder=" "
            />
            <button type="button" class="qna-search-btn" @click="onSearch">
              <img src="@/img/Feed/search_btn.png" alt="" />
            </button>
          </div>
        </form>
        <div>
          <button
            type="button"
            class="qna-write insert-btn big-ctlbtn"
            @click="onWrite"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,

      AllQna: [
        {
          no: 4,
          category: "",
          title: "  ",
          writer: "",
          date: "2020-11-12",
        },
        {
          no: 3,
          category: "",
          title: "    ",
          writer: "",
          date: "2017-11-22",
        },
        {
          no: 2,
          category: "",
          title: "  ",
          writer: "",
          date: "2020-11-12",
        },
        {
          no: 1,
          category: "",
          title: "    ",
          writer: "",
          date: "2017-11-22",
        },
        {
          no: 16,
          category: "",
          title: "  ",
          writer: "",
          progress: "",
          date: "2020-11-12",
        },
        {
          no: 15,
          category: "",
          title: "    ",
          writer: "",
          progress: "",
          date: "2017-11-22",
        },
        {
          no: 14,
          category: "",
          title: "  ",
          writer: "",
          progress: "",
          date: "2020-11-12",
        },
        {
          no: 13,
          category: "",
          title: "    ",
          writer: "",
          progress: "",
          date: "2017-11-22",
        },
        {
          no: 12,
          category: "",
          title: "  ",
          writer: "",
          progress: "",
          date: "2020-11-12",
        },
        {
          no: 11,
          category: "",
          title: "    ",
          writer: "",
          progress: "",
          date: "2017-11-22",
        },
        {
          no: 10,
          category: "",
          title: "  ",
          writer: "",
          progress: "",
          date: "2020-11-12",
        },
        {
          no: 9,
          category: "",
          title: "    ",
          writer: "",
          progress: "",
          date: "2017-11-22",
        },
        {
          no: 8,
          category: "",
          title: "  ",
          writer: "",
          progress: "",
          date: "2020-11-12",
        },
        {
          no: 7,
          category: "",
          title: "    ",
          writer: "",
          progress: "",
          date: "2017-11-22",
        },
        {
          no: 6,
          category: "",
          title: "  ",
          writer: "",
          progress: "",
          date: "2020-11-12",
        },
        {
          no: 5,
          category: "",
          title: "    ",
          writer: "",
          progress: "",
          date: "2017-11-22",
        },
        // ... more items
      ],
    };
  },
  computed: {
    paginatedQnA() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.AllQna.slice(start, end);
    },
    pageCount() {
      return Math.ceil(this.AllQna.length / this.pageSize);
    },
  },
  created() {
    this.$emit("bgImage", "type3");
  },
  methods: {
    toggle(index) {
      this.faqs[index].open = !this.faqs[index].open;
    },
    setCurrentPage(page) {
      //       
      if (page >= 1 && page <= this.pageCount) {
        this.currentPage = page; //    

        //   'update:currentPage'  
        // v-model .sync modifier    
        this.$emit("update:currentPage", page);
      }
    },
    // ''    
    goToPreviousPage() {
      if (this.currentPage > 1) {
        this.setCurrentPage(this.currentPage - 1);
      }
    },
    // ''    
    goToNextPage() {
      if (this.currentPage < this.pageCount) {
        this.setCurrentPage(this.currentPage + 1);
      }
    },
    onSearch() {
      console.log(" :", this.searchQuery);
    },
    onWrite() {
      console.log("  ");
      this.$router.push({ name: "WriteQnA" });
    },
  },
};
</script>

