<template>
  <div id="app">
    <div class="container1">
      <FeedRecommend
        v-if="modal_Check"
        @close-modal="closeModal"
        :RecommendList="RecommendList"
      />
      <form>
        <div class="bg">
          <div>
            <h1 class="page-upload-title"> </h1>
          </div>

          <!--   Area -->
          <div class="part-area">
            <p class="part-title"></p>
            <div class="upload-image-area">
              <div v-for="(item, idx) in 5" :key="idx">
                <label :for="'file' + (idx + 1)">
                  <div class="file" :class="{ upfile: idx === 0 }">
                    <img v-if="idx === 0" src="@/img/Feed/camera.png" alt="" />
                  </div>
                </label>
                <input
                  v-if="idx === 0"
                  type="file"
                  :id="'file' + (idx + 1)"
                  accept="image/*"
                  hidden
                />
              </div>
            </div>
          </div>

          <div class="part-area">
            <p class="part-title"></p>
            <textarea
              id="contents"
              cols="68"
              rows="7"
              placeholder="  ."
              v-model="myFeed.contents"
            ></textarea>
          </div>

          <div class="part-area">
            <p class="part-title"> </p>
            <div class="hashtag-area">
              <span
                @click="toggleActive(oidx, 0)"
                v-for="(originHash, oidx) in myFeed.originHashtag"
                :key="oidx"
                :class="getTagClasses(oidx, 0)"
              >
                #{{ originHash }}
              </span>
            </div>
          </div>

          <div class="part-area">
            <div>
              <p class="part-title">
                 <br />
                <span style="color: #a7a7a7"
                  >   !</span
                >
              </p>
            </div>
            <div class="hashtag-area">
              <span
                @click="toggleActive(hidx, 1)"
                v-for="(hash, hidx) in hashtag"
                :key="hidx"
                :class="getTagClasses(hidx, 1)"
              >
                #{{ hash }}
              </span>
            </div>
          </div>

          <!--    -->
          <div class="part-area">
            <button
              type="button"
              class="part-title recommend-modal-open-btn else-btn"
              @click="openModal"
            >
                
            </button>
          </div>

          <div class="updateFeed-btn-area">
            <button id="uploadFeed"> </button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import FeedRecommend from "@/components/client/feed/FeedRecommendModal.vue";

export default {
  name: "UpdateFeed",
  data() {
    return {
      myFeed: {
        uid: "abc",
        originHashtag: ["", "", ""],
        upimage: ["feed1.png", "feed2.jpg", "feed2.jpg"],
        contents:
          "            .   .      ",
      },
      hashtag: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      activeTags: [[], []],
      modal_Check: false,
      RecommendList: [
        {
          rdate: "9 23",
          recList: [
            {
              storeName: "  ",
              stAddress: "   1",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: ["", "", "", "", ""],
              heartOnOff: "on",
            },
            {
              storeName: " ",
              stAddress: "   2",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: ["", "", ""],
              heartOnOff: "off",
            },
            {
              storeName: " ",
              stAddress: "   2",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: ["", "", ""],
              heartOnOff: "off",
            },
          ],
        },
        {
          rdate: "9 22",
          recList: [
            {
              storeName: " ",
              stAddress: "   3",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
              ],
              heartOnOff: "on",
            },
            {
              storeName: "  ",
              stAddress: "   1",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: [
                "",
                "",
                "",
                "",
                "",
                "",
              ],
              heartOnOff: "on",
            },
          ],
        },
        {
          rdate: "9 21",
          recList: [
            {
              storeName: " ",
              stAddress: "   2",
              img: require("@/img/Feed/bul.png"),
              hashtag: ["", "", ""],
              heartOnOff: "off",
            },
          ],
        },
      ],
    };
  },
  created() {
    for (var i = 0; i < this.myFeed.originHashtag.length; i++) {
      this.activeTags[0][i] = i;
    }
    this.bgImage();
  },
  methods: {
    bgImage() {
      var newImage = "type4";
      this.$emit("bgImage", newImage);
    },
    toggleActive(index, num) {
      const indexOfTag = this.activeTags[num].indexOf(index);

      if (indexOfTag === -1) {
        this.activeTags[num].push(index);
      } else {
        this.activeTags[num].splice(indexOfTag, 1);
      }
    },

    getTagClasses(index, num) {
      return {
        hashtag: true,
        "cursor-p": true,
        "hash-active": this.activeTags[num].includes(index),
      };
    },

    openModal() {
      this.modal_Check = !this.modal_Check;
    },

    closeModal() {
      this.modal_Check = false;
    },
  },
  components: { FeedRecommend },
};
</script>
<style scoped>
@import url("@/css/client/feed/uploadFeed.css");
.hashtag-area {
  margin-top: -10px;
}

.btn-area {
  width: 660px;
}

/*   */
#uploadFeed {
  background-color: #ffdbb0;
}

#uploadFeed:hover {
  background-color: #ffa83f;
}
</style>
