import { createRouter, createWebHistory } from "vue-router";

import Home from "@/pages/client/main/Home.vue"; //  

//  member--------------------------------------
import Login from "@/pages/client/member/Login.vue";
import Signup from "@/pages/client/member/Signup.vue"; // 
import MyPage from "@/pages/client/member/MyPage.vue"; // 
import UserConfirmation from "@/pages/client/member/UserConfirmation.vue"; //        
import UpdateMyInfo from "@/pages/client/member/UpdateMyInfo.vue"; //   
import SelectHobby from "@/pages/client/member/SelectHobby.vue"; //    

//   stress--------------------------------------
import Emotional_analysis from "@/pages/client/stress/Emotional_analysis.vue"; //   
import Total_stress from "@/pages/client/stress/Total_stress.vue"; //  
import Emotional_report from "@/pages/client/stress/Emotional_report.vue"; //   

//  feed--------------------------------------
import Feed from "@/pages/client/feed/Feed.vue";
import InsertFeed from "@/pages/client/feed/InsertFeed.vue"; //  
import UpdateFeed from "@/pages/client/feed/UpdateFeed.vue"; //  
import MyFeed from "@/pages/client/feed/MyFeed.vue"; //   

//   myPlace--------------------------------------
import MyPlaceDiary from "@/pages/client/myPlace/MyPlaceDiary.vue"; //   (  + )
import MyFavoritePlace from "@/pages/client/myPlace/MyFavoritePlace.vue"; //  

// --------------------------------------
import Test from "@/pages/test.vue"; // test 

// QnA--------------------------------------
import QnA from "@/pages/client/qna/QnA.vue";
import MyQnA from "@/components/client/qna/MyQnA.vue";
import WriteQnA from "@/pages/client/qna/WriteQnA.vue";
import UpdateQnA from "@/pages/client/qna/UpdateQnA.vue";
import DetailQnA from "@/pages/client/qna/DetailQnA.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/Login",
    name: "Login",
    component: Login,
  },
  {
    path: "/Signup",
    name: "Signup",
    component: Signup,
  },
  {
    path: "/Feed",
    name: "Feed",
    component: Feed,
  },
  {
    path: "/InsertFeed",
    name: "InsertFeed",
    component: InsertFeed,
  },
  {
    path: "/UpdateFeed",
    name: "UpdateFeed",
    component: UpdateFeed,
  },
  {
    path: "/MyFeed",
    name: "MyFeed",
    component: MyFeed,
  },
  {
    path: "/MyPlaceDiary",
    name: "MyPlaceDiary",
    component: MyPlaceDiary,
  },
  {
    path: "/MyPage",
    name: "MyPage",
    component: MyPage,
  },
  {
    path: "/UpdateMyInfo",
    name: "UpdateMyInfo",
    component: UpdateMyInfo,
  },
  {
    path: "/UserConfirmation",
    name: "UserConfirmation",
    component: UserConfirmation,
  },
  {
    path: "/SelectHobby",
    name: "SelectHobby",
    component: SelectHobby,
  },
  {
    path: "/Emotional_analysis",
    name: "Emotional_analysis",
    component: Emotional_analysis,
  },
  {
    path: "/Total_stress",
    name: "Total_stress",
    component: Total_stress,
  },
  {
    path: "/MyFavoritePlace",
    name: "MyFavoritePlace",
    component: MyFavoritePlace,
  },
  {
    path: "/Test",
    name: "Test",
    component: Test,
  },
  {
    path: "/Emotional_report",
    name: "Emotional_report",
    component: Emotional_report,
  },
  {
    path: "/QnA",
    name: "QnA",
    component: QnA,
  },
  {
    path: "/MyQnA",
    name: "MyQnA",
    component: MyQnA,
  },
  {
    path: "/WriteQnA",
    name: "WriteQnA",
    component: WriteQnA,
  },
  {
    path: "/UpdateQnA",
    name: "UpdateQnA",
    component: UpdateQnA,
  },
  {
    path: "/DetailQnA",
    name: "DetailQnA",
    component: DetailQnA,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
