<template>
  <div class="container1">
    <div class="qna-container">
      <div class="faq-container">
        <h2 class="faq-title"> QnA </h2>
      </div>

      <div class="nav-bar">
        <router-link to="/QnA" class="nav-item" exact-active-class="active"
          >Q&A</router-link
        >
        <router-link to="/myQnA" class="nav-item" exact-active-class="active"
          >  </router-link
        >
        <!-- ...   ... -->
      </div>
      <table class="qna-table">
        <thead>
          <tr>
            <th class="qna-tr"></th>
            <th class="qna-tr"></th>
            <th></th>
            <th class="qna-tr"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in paginatedQnA" :key="item.no">
            <td class="qna-tr">{{ item.no }}</td>
            <td class="qna-tr">{{ item.category }}</td>
            <td>
              <a href="/DetailQnA">{{ item.title }}</a
              ><span
                class="qna-badge"
                :class="{
                  notAnswered: item.progress === '',
                  answered: item.progress === '',
                }"
                >{{ item.progress }}</span
              >
            </td>
            <td class="qna-tr">{{ item.date }}</td>
          </tr>
        </tbody>
      </table>

      <div class="pagination">
        <button
          class="pagination-button"
          :class="{ disabled: currentPage === 1 }"
          @click="setCurrentPage(currentPage - 1)"
          v-if="currentPage > 1"
        >
          &lt;
        </button>

        <button
          v-for="page in pageCount"
          :key="page"
          :class="{ 'pagination-button': true, active: currentPage === page }"
          @click="setCurrentPage(page)"
        >
          {{ page }}
        </button>

        <button
          class="pagination-button"
          :class="{ disabled: currentPage === pageCount }"
          @click="setCurrentPage(currentPage + 1)"
          v-if="currentPage < pageCount"
        >
          &gt;
        </button>
      </div>
      <div class="qna-search-container">
        <form id="qna-search-form" @submit.prevent="onSearch">
          <div class="qna-search-area">
            <input
              class="qna-search-input"
              v-model="searchQuery"
              type="text"
              placeholder="Search"
            />
            <button type="button" class="qna-search-btn" @click="onSearch">
              <img src="@/img/Feed/search_btn.png" alt="" />
            </button>
          </div>
        </form>
        <div>
          <button
            type="button"
            class="big-ctlbtn insert-btn qna-Write"
            @click="onWrite"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,

      MyQna: [
        {
          no: 13,
          category: "",
          title: "    ",
          date: "2017-11-22",
          progress: "",
        },
        {
          no: 12,
          category: "",
          title: "  ",
          date: "2020-11-12",
          progress: "",
        },
        {
          no: 11,
          category: "",
          title: "    ",
          date: "2017-11-22",
          progress: "",
        },
        {
          no: 10,
          category: "",
          title: "  ",
          date: "2020-11-12",
          progress: "",
        },
        {
          no: 9,
          category: "",
          title: "    ",
          date: "2017-11-22",
          progress: "",
        },
        {
          no: 8,
          category: "",
          title: "  ",
          date: "2020-11-12",
          progress: "",
        },
        {
          no: 7,
          category: "",
          title: "    ",
          date: "2017-11-22",
          progress: "",
        },
        {
          no: 6,
          category: "",
          title: "  ",
          date: "2020-11-12",
          progress: "",
        },
        {
          no: 5,
          category: "",
          title: "    ",
          date: "2017-11-22",
          progress: "",
        },
        {
          no: 4,
          category: "",
          title: "  ",
          date: "2020-11-12",
          progress: "",
        },
        {
          no: 3,
          category: "",
          title: "    ",
          date: "2017-11-22",
          progress: "",
        },
        {
          no: 2,
          category: "",
          title: "  ",
          date: "2020-11-12",
          progress: "",
        },
        {
          no: 1,
          category: "",
          title: "    ",
          date: "2017-11-22",
          progress: "",
        },
      ],
    };
  },
  computed: {
    paginatedQnA() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.MyQna.slice(start, end);
    },
    pageCount() {
      return Math.ceil(this.MyQna.length / this.pageSize);
    },
  },
  created() {
    this.$emit("bgImage", "type3");
  },
  methods: {
    toggle(index) {
      this.faqs[index].open = !this.faqs[index].open;
    },
    setCurrentPage(page) {
      //       
      if (page >= 1 && page <= this.pageCount) {
        this.currentPage = page; //    

        //   'update:currentPage'  
        // v-model .sync modifier    
        this.$emit("update:currentPage", page);
      }
    },
    // ''    
    goToPreviousPage() {
      if (this.currentPage > 1) {
        this.setCurrentPage(this.currentPage - 1);
      }
    },
    // ''    
    goToNextPage() {
      if (this.currentPage < this.pageCount) {
        this.setCurrentPage(this.currentPage + 1);
      }
    },
    onSearch() {
      console.log(" :", this.searchQuery);
    },
    onWrite() {
      console.log("  ");
      this.$router.push({ name: "WriteQnA" });
    },
  },
};
</script>

<style scoped>
@import url("@/css/client/qna/qnaBoard.css");
.qna-tr {
  text-align: center;
}
.active {
  font-weight: bold; /*     */
  color: #2eb4c9; /*    */
  border-bottom: 3px solid #2eb4c9; /*    */
}
</style>
