<template>
  <div class="container1">
    <FeedRecommend
      v-if="modal_Check"
      @close-modal="closeModal"
      :RecommendList="RecommendList"
    />
    <form>
      <div class="bg">
        <div>
          <h1 class="page-upload-title"> </h1>
        </div>

        <!--   Area -->
        <div class="part-area">
          <p class="part-title"> </p>
          <div class="upload-image-area">
            <div v-for="(item, idx) in 5" :key="idx">
              <label :for="'file' + (idx + 1)">
                <div class="file" :class="{ upfile: idx === 0 }">
                  <img v-if="idx === 0" src="@/img/Feed/camera.png" alt="" />
                </div>
              </label>
              <input
                v-if="idx === 0"
                type="file"
                :id="'file' + (idx + 1)"
                accept="image/*"
                hidden
              />
            </div>
          </div>
        </div>

        <div class="part-area">
          <p class="part-title"> </p>
          <textarea
            id="contents"
            cols="68"
            rows="7"
            placeholder="  ."
          ></textarea>
        </div>

        <div class="part-area">
          <div>
            <p class="part-title">
               <br />
              <span style="color: #a7a7a7"
                >   !</span
              >
            </p>
          </div>
          <div class="hashtag-area">
            <span
              @click="toggleActive(hidx)"
              v-for="(hash, hidx) in hashtag"
              :key="hidx"
              :class="getTagClasses(hidx)"
            >
              #{{ hash }}
            </span>
          </div>
        </div>

        <!--    -->
        <div class="part-area">
          <button
            type="button"
            class="part-title recommend-modal-open-btn else-btn"
            @click="openModal"
          >
              
          </button>
        </div>

        <div class="updateFeed-btn-area">
          <button id="uploadFeed"> </button>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
import FeedRecommend from "@/components/client/feed/FeedRecommendModal.vue";

export default {
  name: "InsertFeed",
  data() {
    return {
      isLoggedIn: false,
      AccessToken: "",
      uid: "abc",
      hashtag: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      activeTags: [],
      RecommendList: [
        {
          rdate: "9 23",
          recList: [
            {
              storeName: "  ",
              stAddress: "   1",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: ["", "", "", "", ""],
              heartOnOff: "on",
            },
            {
              storeName: " ",
              stAddress: "   2",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: ["", "", ""],
              heartOnOff: "off",
            },
            {
              storeName: " ",
              stAddress: "   2",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: ["", "", ""],
              heartOnOff: "off",
            },
          ],
        },
        {
          rdate: "9 22",
          recList: [
            {
              storeName: " ",
              stAddress: "   3",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
              ],
              heartOnOff: "on",
            },
            {
              storeName: "  ",
              stAddress: "   1",
              img: require("@/img/Feed/bul.png"),
              link: "#",
              hashtag: [
                "",
                "",
                "",
                "",
                "",
                "",
              ],
              heartOnOff: "on",
            },
          ],
        },
        {
          rdate: "9 21",
          recList: [
            {
              storeName: " ",
              stAddress: "   2",
              img: require("@/img/Feed/bul.png"),
              hashtag: ["", "", ""],
              heartOnOff: "off",
            },
          ],
        },
      ],
      modal_Check: false,
    };
  },
  created() {
    this.bgImage();
    this.getToken();
  },
  methods: {
    bgImage() {
      var newImage = "type4";
      this.$emit("bgImage", newImage);
    },
    getToken() {
      this.AccessToken = localStorage.getItem("jwtToken");
      console.log(this.AccessToken);
      if (this.AccessToken != null) {
        this.isLoggedIn = true;
      } else {
        this.isLoggedIn = false;
        this.$router.push("/login");
      }
    },
    toggleActive(index) {
      const indexOfTag = this.activeTags.indexOf(index);

      if (indexOfTag === -1) {
        this.activeTags.push(index);
      } else {
        this.activeTags.splice(indexOfTag, 1);
      }
    },
    getTagClasses(index) {
      return {
        hashtag: true,
        "cursor-p": true,
        "hash-active": this.activeTags.includes(index),
      };
    },

    openModal() {
      this.modal_Check = true;
    },

    closeModal() {
      this.modal_Check = false;
    },
  },
  components: { FeedRecommend },
};
</script>
<style scoped>
@import url("@/css/client/feed/uploadFeed.css");
</style>
