<script>
export default {
  name: "total_stress",
  data() {
    return {
      gaugeWidth: "0%", //  
      ratNumber: [10, 20, 30, 40, 50, 60, 70, 80, 90],
      stressRate: 0,
    };
  },
  created() {
    // background-image 
    this.bgImage();
  },
  methods: {
    // background-image 
    bgImage() {
      var newImage = "type2";
      this.$emit("bgImage", newImage);
    },
    scrollLeft() {
      var slider = document.querySelector(".recommend-place");
      slider.scrollTo({
        left: 0,
        behavior: "smooth",
      });
    },
    scrollRight() {
      const slider = document.querySelector(".recommend-place");
      slider.scrollTo({
        left: slider.scrollWidth,
        behavior: "smooth",
      });
    },
    toMyPlaceDiary() {
      this.$router.push("/MyPlaceDiary");
    },
    toMain() {
      this.$router.push("/");
    },
  },
  mounted() {
    let currentWidth = 0;
    const targetWidth = 78; //    
    this.stressRate = targetWidth;

    const animationDuration = 5000;
    const interval = 25;

    const step = (targetWidth - currentWidth) / (animationDuration / interval);

    const animate = () => {
      if (currentWidth < targetWidth) {
        currentWidth += step;
        this.gaugeWidth = currentWidth + "%";
        this.stressRate = Math.round(currentWidth);
        requestAnimationFrame(animate);
      } else {
        this.gaugeWidth = targetWidth + "%"; //     
        this.stressRate = targetWidth;
      }
    };
    animate();
  },
};
</script>

<style scoped>
