<template>
  <div>
    <div class="container1">
      <div class="login-container">
        <!--    -->
        <div class="left-box">
          <h2> </h2>
          <img src="@/assets/icon/login/login_icon.png" />
          <p>... ?</p>
          <a href="/Signup" class="big-ctlbtn else-btn"></a>
        </div>

        <!--    -->
        <div class="login-box">
          <div class="login-inner-box">
            <form
              method="post"
              autocomplete="off"
              @submit.prevent="loginSubmit"
            >
              <h1></h1>
              <div class="form-input-box">
                <!--  -->
                <div class="form-input">
                  <label for="userId"></label>
                  <input
                    type="text"
                    name="userId"
                    id="userId"
                    class="input-text"
                    v-model="userId"
                  />
                </div>
                <!--  -->
                <div class="form-input">
                  <label for="password"></label>
                  <input
                    type="password"
                    name="password"
                    id="password"
                    class="input-text"
                    v-model="pwd"
                  />
                </div>
                <div class="form-check">
                  <div class="remember-checkbox">
                    <input type="checkbox" name="remember" id="remember" />
                    <label for="remember">  </label>
                  </div>
                </div>
                <button
                  class="big-ctlbtn insert-btn"
                  id="login-submit-btn"
                  @submit="loginSubmit"
                >
                  
                </button>

                <div class="login-search-box">
                  <button @click="findIdToggleModal" @click.prevent="submit">
                     
                  </button>
                  <button @click="findPwdToggleModal" @click.prevent="submit">
                     
                  </button>
                </div>
              </div>
            </form>
          </div>
          <div class="social-login-box">
            <a href="#"
              ><img src="@/assets/icon/login/kakao_login_icon.png"
            /></a>
            <a href="#"
              ><img src="@/assets/icon/login/naver_login_icon.png"
            /></a>
          </div>
        </div>
      </div>
    </div>
    <FindByIdModal :modalOpen="isIdModalOpen" @modalClose="findIdToggleModal" />
    <FindByPwdModal
      :modalOpen="isPwdModalOpen"
      @modalClose="findPwdToggleModal"
    />
  </div>
</template>
<script>
import FindByIdModal from "@/components/client/member/FindByIdModal.vue";
import FindByPwdModal from "@/components/client/member/FindByPwdModal.vue";
import axios from "axios";

export default {
  name: "Login",
  data() {
    return {
      isIdModalOpen: false,
      isPwdModalOpen: false,
      userId: "",
      pwd: "",
      AccessToken: "",
    };
  },
  components: {
    FindByIdModal,
    FindByPwdModal,
  },
  created() {
    this.bgImage();
  },
  methods: {
    bgImage() {
      var newImage = "type3";
      this.$emit("bgImage", newImage);
    },
    findIdToggleModal() {
      this.isIdModalOpen = !this.isIdModalOpen;
    },
    findPwdToggleModal() {
      console.log("  ");
      this.isPwdModalOpen = !this.isPwdModalOpen;
    },
    loginSubmit() {
      axios
        .post(
          `http://${process.env.VUE_APP_BACK_END_URL}/api/auth/login`,
          {
            id: this.userId,
            pwd: this.pwd,
          },
          {
            headers: {
              "Content-Type": "application/json",
              Authorization: `Bearer ${this.AccessToken}`,
            },
            withCredentials: true,
          }
        )
        .then((res) => {
          console.log(res);
          if (!res.data.access_token == "") {
            alert(" ");
            localStorage.setItem("jwtToken", res.data.access_token);
            this.$router.replace("/");
          } else {
            alert(" ");
          }
        })
        .catch((error) => {
          console.error(" ", error);
          alert("   .");
        });
    },
  },
};
</script>
<style scoped>
@import url("@/css/client/member/login.css");
.container1 {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
