        images: [
          require("@/img/Feed/feed1.png"),
          require("@/img/Feed/feed3.jpg"),
          require("@/img/Feed/feed2.jpg"),
        ],
        content:
          "   ...   ...        ...   ...       ",
        likes: 22,
        rDate: "2023-12-26",
        comments: 5,
      },
      comments: [
        {
          profileImage: require("@/img/Feed/no_profile.png"),
          uid: "abc",
          nickname: "",
          cDate: "2023-12-30",
          comment: " !",
        },
        {
          profileImage: require("@/img/Feed/no_profile.png"),
          uid: "abcd",
          nickname: "",
          cDate: "2023-12-30",
          comment:
            ",  !         .              .    ?      ?      !",
        },
        {
          profileImage: require("@/img/Feed/no_profile.png"),
          uid: "abc",
          nickname: "",
          cDate: "2023-12-29",
          comment: " 1",
        },
        {
          profileImage: require("@/img/Feed/no_profile.png"),
          uid: "abcd",
          nickname: "",
          cDate: "2023-12-28",
          comment: " 2",
        },
        {
          profileImage: require("@/img/Feed/no_profile.png"),
          uid: "abc",
          nickname: "",
          cDate: "2023-12-27",
          comment: " 3",
        },
      ],
      recommend: {
        name: "  ",
